				.nav-right {
					@media (max-width: 991px) {
						display: block;
						text-align: left;
					}

					.nav-btn {
						background: transparent;
						color: #fff;
						box-shadow: none;
						border: 2px solid rgba(255, 255, 255, 0.302);
						border-radius: 4px;
						padding: 7px 24px;
						font-size: 16px;

						&:hover {
							background: #fff;
							border-color: #fff;
							color: $color__theme-2;
						}

						@media (max-width: 991px) {
							border-color: $color__theme-2;
							color: $color__theme-2;

							&:hover {
								color: #fff;
								background: $color__theme-2;
							}
						}
					}
				}
			}
		}

		.site-main-menu {
			li {
				a {
					color: #2b2350;

					&:after {
						display: none;
					}

					&:hover,
					&.current_page {
						color: $color__theme-2;
					}
				}

				&.menu-item-has-children {
					&:after {
						color: #2b2350;
					}
				}

				.sub-menu {
					li {
						a {
							&:hover,
							&.current_page {
