.logo-sticky {
	display: none;
}

.pix-header-fixed {
	top: 0;
	left: 0;
	right: 0;
	position: fixed;
	z-index: 999999;
	box-shadow: 0px 10px 20px 0px rgba(79, 35, 35, 0.08);
	animation: stickySlideDown 0.65s cubic-bezier(0.23, 1, 0.32, 1) both;
	background: #fff;

	.header-inner {
		.site-logo {
			padding: 18px 0;

			.logo-sticky {
				display: block;
			}

			.logo-main {
				display: none;
			}
		}

		.site-nav {
			.nav-right {
				.nav-btn {
					background: transparent;
					color: $color__theme;
					box-shadow: none;
					border-color: $color__theme;

					&:hover {
						background: $color__theme;
						color: #fff;
					}
				}
			}

			&.nav-two {
				.nav-right {
					.nav-btn,
					.nav-btn.style-two {
						border-color: $color__theme-2;
						color: $color__theme-2;

						&:hover {
							background: $color__theme-2;
							color: #fff;
							border-color: $color__theme-2;
						}
					}
				}

				.site-main-menu {
					li {
						a {
							&:hover,
							&.current_page {
								color: $color__theme-2;
							}
						}
					}
				}
			}
		}
	}

	.site-main-menu {
		li {
			padding: 28px 0;

			&.menu-item-has-children:after {
				color: #333;
			}

			a {
				color: #333;

				&:after {
					display: none;
				}

				&:hover,
				&.current_page {
					color: $color__theme;

					&:before,
					&:after {
						background: $color__theme;
					}
				}
			}
		}
	}

	.site-logo {
		.main-logo {
			display: none;
		}

		.sticky-logo {
			display: block;
		}
	}

	&.header-five {
		.header-inner {
			.site-nav {
				.nav-right {
					.nav-btn {
						color: $color__theme-2;
						border: 2px solid $color__theme-2;

						&:hover {
							background: $color__theme-2;
							border-color: $color__theme-2;
							color: #fff;
						}
					}
				}
			}
		}
	}
}

.mask-overlay {
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	z-index: 3333;
	top: 0;
	left: 0;
}

.close-menu {
	display: none;
}

.home-color-two {
	.return-to-top {
		&:hover {
			&:after {
				transform: scale(1.07);
				background: $color__theme-2;
				box-shadow: 0px 10px 20px 8px rgba(0, 0, 0, 0.15);
			}

			> i {
				text-shadow: 0px -50px #fff, 0px 0px #fff;
			}
		}
	}
}

@media (max-width: 991px) {
	.toggle-menu {
		position: absolute;
		left: 0;
		width: 26px;
		top: 50%;
		transform: translateY(-50%);
		display: block;
		height: 24px;

		.bar {
			width: 18px;
			height: 2px;
			display: block;
			float: left;
			margin: 3px auto;
			transition: all 0.3s ease-in-out;
			background: #fff;

			&:nth-child(2) {
				width: 24px;
			}
		}
	}

	.site-header {
		.header-inner {
			text-align: center;
			padding: 15px 0;

			.site-nav {
				.nav-right {
					margin-left: 20px;

					.nav-btn {
						padding: 8px 22px;
					}
				}
			}
		}

		.site-mobile-logo {
			display: block;
		}

		.site-main-menu {
			li {
				margin: 0 15px;
			}
		}

		.site-logo {
			display: none;
		}

		&.header-two {
			&.toggle-light {
				.toggle-menu .bar {
					background: #fff;
				}
			}
		}

		&.header-five,
		&.header-six {
			.toggle-menu .bar {
				background: #333;
			}
		}
	}

	.site-header {
		.site-nav {
			position: fixed;
			width: 320px !important;
			height: 100vh;
			background: #fff;
			top: 0;
			left: -100%;
			display: block !important;
			transition: all 0.5s ease-in-out;
			overflow: scroll;
			box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);

			.site-main-menu {
				display: block;
				width: 100%;
				padding-bottom: 20px;
				text-align: left;

				li {
					padding: 0;
					margin: 0;
					border-bottom: 1px solid #f1f2f3;

					&:first-child {
						border-top: 1px solid #f1f2f3;
					}

					a {
						display: block;
						color: #888888;
						padding: 6px 35px;
						font-weight: 400;

						&:after {
							display: none;
						}
					}

					&.menu-item-has-children {
						position: relative;

						&:after {
							display: none;
						}

						a:hover,
						&:focus {
							color: $color__theme;
						}

						i {
							position: absolute;
							top: 6px;
							right: 20px;
							display: block;
							width: 30px;
							height: 30px;
							text-align: center;
							line-height: 30px;
							z-index: 222;
						}

						.sub-menu {
							position: static;
							box-shadow: none;
							opacity: 1;
							width: auto;
							visibility: visible;
							background: transparent;
							padding: 0;
							display: none;
							transition: none;

							li {
								padding: 0;

								&:first-child {
									border-top: 1px solid #f1f2f3;
								}

								&:last-child {
									border-bottom: none;
									padding-bottom: 0;
								}

								a {
									color: #888888;
									font-weight: 400;
									padding: 5px 35px 5px 45px;
								}

								&.menu-item-has-children {
									.sub-menu {
										opacity: 1;
										visibility: visible;
										position: relative;
										left: 0;
										top: auto;

										li {
											a {
												padding-left: 60px;
											}
										}
									}

									&:after {
										content: "\33";
										color: #fff;
									}
								}
							}
						}
					}
				}
			}

			.nav-right {
				// display: none;

				text-align: left;
				padding-left: 13px;
			}

			&.nav-two {
				.site-main-menu {
					li.menu-item-has-children {
						&:hover,
						&:focus {
							color: $color__theme-2;

							&:after {
								color: $color__theme-2;
							}
						}

						a {
							&:hover,
							&:focus {
								color: $color__theme-2;
							}
						}
					}
				}
			}
		}

		&.header-two {
			.toggle-menu .bar {
				background: $color__primary;
			}
		}
	}

	.header-inner {
		.site-mobile-logo {
			img {
				max-height: 30px;
				transition: all 0.3s ease-in-out;
			}
		}
	}

	.pix-header-fixed {
		.toggle-menu {
			top: 30px;

			.bar {
				background: #333;
			}
		}

		.site-main-menu {
			li {
				a {
					color: #fff;
				}

				&.menu-item-has-children:after {
					color: #fff;
				}
			}
		}

		.header-inner .site-logo {
			padding: 12px 0;
		}

		&.header-two {
			&.toggle-light {
				.toggle-menu .bar {
					background: #000;
				}
			}
		}
	}

	.sidebar-open {
		.site-header {
			.site-nav {
				left: 0;
			}
		}

		.menu-wrapper:before {
			opacity: 1;
			visibility: visible;
		}
	}

	.close-menu {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		top: 0;
		text-align: right;
		width: 100%;
		left: 0;
		padding: 2px;
		padding: 30px 20px;

		i {
			font-size: 36px;
			color: $color__primary;
		}

		span {
			font-size: 15px;
		}
	}

	.header-dark {
		.toggle-menu .bar {
			background: $color__primary;
		}
	}
}

@media (max-width: 420px) {
	.site-header .site-nav {
		width: 100% !important;
	}
}
