.saaspik-icon-box-wrapper {
	margin-bottom: 30px;

	&.style-one {
		margin-top: 16px;

		.saaspik-icon-box-icon {
			margin-bottom: 40px;
			height: 150px;
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				font-size: 22px;
				font-weight: 500;
				line-height: 30px;
				margin-bottom: 0;

				a {
					color: $color__primary;

					&:hover {
						color: $color__theme;
					}
				}
			}
		}
	}

	.saaspik-icon-box-icon {
		margin-bottom: 32px;
	}

	.pixsass-icon-box-content {
		.pixsass-icon-box-title {
			font-size: 20px;
			font-weight: 600;
			line-height: 30px;
			margin-bottom: 20px;
			transition: all 0.3s ease-in-out;

			a {
				color: $color__primary;

				&:hover {
					color: $color__theme;
				}
			}
		}

		p {
			margin: 0;
		}
	}

	&.style-two {
		margin-bottom: 88px;

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				font-size: 20px;
				font-weight: 600;
				line-height: 30px;
				margin-bottom: 10px;

				a {
					color: $color__primary;

					&:hover {
						color: $color__theme;
					}
				}
			}
		}
	}

	&.style-three {
		border-radius: 6px;
		background-color: rgb(255, 255, 255);
		box-shadow: 0px 40px 60px 0px rgba(79, 35, 35, 0.14);
		padding: 40px 50px;
		display: flex;
		flex-wrap: wrap;
		position: relative;
		z-index: 22;
		align-items: center;

		.saaspik-icon-box-icon {
			width: 70px;
			margin-right: 30px;
			margin-bottom: 0;
		}

		.pixsass-icon-box-content {
			flex: 1;

			p {
				margin: 0;
			}
		}
	}

	&.style-four {
		border-radius: 4px;
		border: 1px solid #ece4e4;
		padding: 60px 40px 32px;
		transition: all 0.3s ease-in-out;
		position: relative;
		overflow: hidden;

		.layer {
			position: absolute;
			top: -100%;
			left: 0;
			width: 100%;
			z-index: -1;
			transition: all 0.6s ease-in-out;
		}

		.saaspik-icon-box-icon {
			margin-bottom: 44px;
			height: 80px;
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				font-size: 22px;
				font-weight: 500;
				margin-bottom: 25px;

				a {
					color: $color__primary;

					&:hover {
						color: $color__theme;
					}
				}
			}

			p {
				margin-bottom: 22px;
			}

			.more-btn {
				font-size: 35px;
				color: #bdb4b4;
				display: inline-block;

				&:hover {
					color: $color__theme;
				}
			}
		}

		&:hover {
			box-shadow: 0px 40px 60px 0px rgba(79, 35, 35, 0.14);

			.layer {
				top: 0;
			}
		}
	}

	&.style-five {
		box-shadow: 0px 30px 60px 0px rgba(52, 27, 159, 0.1);
		padding: 45px 40px;
		background: #fff;

		.saaspik-icon-box-icon {
			height: 60px;
			width: 60px;
			line-height: 60px;
			text-align: center;
			border: 2px solid $color__theme-2;
			color: $color__theme-2;
			font-size: 22px;
			border-radius: 50%;
			transition: all 0.3s ease-in-out;
			margin-bottom: 42px;
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				margin-bottom: 15px;

				a {
					&:hover {
						color: $color__theme-2;
					}
				}
			}

			p {
				margin-bottom: 35px;
			}

			.more-btn {
				color: $color__primary;
				font-weight: 500;

				i {
					vertical-align: middle;
					display: inline-block;
					transition: all 0.3s ease-in-out;
				}

				&:hover {
					color: $color__theme-2;

					i {
						padding-left: 3px;
						color: $color__theme-2;
					}
				}
			}
		}

		&:hover {
			.saaspik-icon-box-icon {
				box-shadow: 0px 20px 30px 0px rgba(75, 42, 222, 0.2);
				background: $color__theme-2;
				color: #fff;
			}
		}
	}

	&.style-six {
		display: flex;
		align-items: center;
		margin-bottom: 40px;
		flex-wrap: wrap;

		.saaspik-icon-box-icon {
			height: 70px;
			width: 70px;
			text-align: center;
			box-shadow: 0px 30px 40px 0px rgba(52, 27, 159, 0.1);
			padding: 15px;
			border-radius: 4px;
			transition: all 0.3s ease-in-out;
			margin-bottom: 0;
			margin-right: 30px;
			background: #fff;
		}

		.pixsass-icon-box-content {
			flex: 1;

			.pixsass-icon-box-title {
				margin-bottom: 15px;
				font-weight: 400;
				margin: 0;

				a {
					&:hover {
						color: $color__theme-2;
					}
				}
			}

			p {
				margin-bottom: 35px;
			}

			.more-btn {
				color: $color__primary;
				font-weight: 500;

				i {
					vertical-align: middle;
					display: inline-block;
					transition: all 0.3s ease-in-out;
				}

				&:hover {
					color: $color__theme-2;

					i {
						padding-left: 3px;
						color: $color__theme-2;
					}
				}
			}
		}
	}

	&.style-seven {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 90px;

		.saaspik-icon-box-icon {
			width: 60px;
			margin-right: 20px;
		}

		.saaspik-icon-box-content {
			flex: 2;

			.saaspik-icon-box-title {
				font-size: 20px;
				color: #081e39;
				font-weight: 600;
				margin-bottom: 13px;

				a {
					color: #081e39;

					&:hover {
						color: $color__theme-2;
					}
				}
			}

			p {
				color: #5e5b74;
				margin-bottom: 17px;
			}

			.read-more {
				color: #5e5b74;
				font-size: 16px;
				font-weight: 500;

				&:hover {
					color: $color__theme-2;
				}
			}
		}
	}

	&.style-eight {
		border-radius: 6px;
		background: #fff;
		box-shadow: 0px 30px 50px 0px rgba(64, 24, 47, 0.08);
		text-align: center;
		padding: 40px 35px;

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				margin-bottom: 15px;

				a {
					&:hover {
						color: $color__theme-4;
					}
				}
			}

			p {
				margin-bottom: 35px;
			}
		}
	}

	&.style-nine {
		margin-bottom: 90px;

		.saaspik-icon-box-icon {
			height: 70px;
			width: 70px;
			background: #fee4f4;
			border-radius: 20px;
			display: flex;
			align-items: center;
			justify-content: center;

			&.color-two {
				background: #fcf5e3;
			}

			&.color-three {
				background: #efe9fe;
			}

			&.color-four {
				background: #eaf7eb;
			}

			&.color-five {
				background: #e4f8fd;
			}

			&.color-six {
				background: #ffeae7;
			}
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				margin-bottom: 10px;

				a {
					&:hover {
						color: $color__theme-4;
					}
				}
			}
		}
	}

	&.style-ten {
		padding: 50px 40px;
		border-radius: 10px;
		background-color: rgb(255, 255, 255);
		box-shadow: 0px 20px 60px 0px rgba(1, 51, 32, 0.1);
		text-align: center;

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				margin-bottom: 15px;
			}

			p {
				margin-bottom: 30px;
			}
		}
	}

	&.style-eleven {
		box-shadow: 0 20px 50px 0 rgba(14, 3, 52, 0.1);
		background: #fff;
		border-radius: 20px;
		padding: 55px 35px;
		transition: all 0.3s ease-in-out;

		.saaspik-icon-box-icon {
			height: 65px;
			display: flex;
			align-items: flex-end;
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				margin-bottom: 13px;
			}

			p {
				color: #615f6b;
			}
		}

		&:hover {
			background: #2c234d;

			.pixsass-icon-box-content {
				.pixsass-icon-box-title {
					color: #fff;

					&:hover {
						color: $color__theme-7;
					}

					a {
						color: #fff;

						&:hover {
							color: $color__theme-7;
						}
					}
				}

				p {
					color: #c3c0d0;
				}
			}
		}
	}

	&.style-twelve {
		padding: 40px 35px;
		border-radius: 6px;
		transition: all 0.3s ease-in-out;
		margin-bottom: 20px;

		.saaspik-icon-box-icon {
			height: 70px;
			width: 70px;
			border-radius: 20px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba(19, 200, 70, 0.102);

			&.color--two {
				background: #fff3dd;
			}

			&.color--three {
				background: #efe1fa;
			}

			&.color--four {
				background: #f6d9e2;
			}

			&.color--five {
				background: #dbddfa;
			}

			&.color--six {
				background: #d1f0f1;
			}
		}

		.pixsass-icon-box-content {
			.pixsass-icon-box-title {
				font-size: 20px;
				font-weight: 500;
				color: #2c234d;

				&:hover {
					color: $color__theme-8;
				}

				a {
					color: #2c234d;

					&:hover {
						color: $color__theme-8;
					}
				}
			}
		}

		&:hover {
			background: #fff;
			box-shadow: 0 30px 70px 0 rgba(5, 34, 85, 0.06);
		}
	}

	&.style-thirteen {
		background-color: #131b2e;
		padding: 40px 18px 30px;
		border-radius: 10px;
		transition: all 0.3s ease-in-out;

		.saaspik-icon-box-icon {
			height: 70px;
			width: 70px;
			background: #343e4f;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			position: relative;
			margin-bottom: 42px;

			&::before {
				content: "";
				position: absolute;
				left: -7px;
				top: -7px;
				right: -7px;
				bottom: -7px;
				border: 1px solid #343e4f;
				border-radius: 50%;
			}
		}

		.saaspik-icon-box-title {
			font-family: "Montserrat Alternates", serif;
			font-size: 20px;
			color: #fff;
			font-weight: 600;
		}

		.saaspik-icon-box-description {
			color: #a1a4ab;
			font-size: 14px;
			line-height: 24px;
			margin-bottom: 30px;
		}

		a.view-link {
			text-align: center;
			display: inline-block;
		}

		&:hover {
			background-color: $color__theme-11;

			.saaspik-icon-box-icon {
				background: $color__black;
			}

			.saaspik-icon-box-title {
				color: $color__black;
			}

			.saaspik-icon-box-description {
				color: #42501e;
			}

			a.view-link {
				svg {
					path {
						stroke: $color__black;
					}
				}
			}
		}
	}

	&.style-fourteen {
		display: flex;
		align-items: center;
		border-radius: 20px;
		border: 1px solid rgba(255, 255, 255, 0.14);
		padding: 15px 40px 15px 15px;
		gap: 20px;
		align-items: center;
		transition: all 0.3s ease-in-out;

		@media (max-width: 480px) {
			flex-direction: column;
		}

		.saaspik-icon-box-icon {
			height: 100px;
			width: 100px;
			display: flex;
			align-items: center;
			justify-content: center;
			min-width: 100px;
			margin: 0;
			border-radius: 10px;
			background: #131b2e;
			transition: all 0.3s ease-in-out;
		}

		.saaspik-icon-box-content-wrappper {
			display: flex;
			align-items: center;
			gap: 16px;
			@media (max-width: 480px) {
				flex-direction: column;
			}
		}

		.saaspik-icon-box-content {
			@media (min-width: 576px) {
				padding-right: 20px;
			}

			@media (max-width: 480px) {
				text-align: center;
			}
		}

		.saaspik-icon-box-title {
			color: #fff;
			font-family: "Montserrat Alternates";
			font-size: 20px;
			font-weight: 600;
		}

		.saaspik-icon-box-description {
			margin-bottom: 0;
			font-size: 14px;
			line-height: 24px;
		}

		&:hover {
			background-color: #131b2e;
			border-color: #131b2e;

			.saaspik-icon-box-icon {
				background: $color__theme-11;
			}
		}
	}
}

