			&:nth-child(1) {
				width: 31px;
				height: 31px;
				border-radius: 50%;
				left: 8%;
				top: 40%;
				background-color: rgba(255, 255, 255, 0.141);
				animation: animationFramesOne 20s infinite linear;
			}

			&:nth-child(2) {
				left: 16%;
				top: 54%;
