#app-feature {
	padding: 88px 0 66px;
	background: #fcfafb;

	@media (max-width: 991px) {
		padding: 60px 0;
	}

	.icon {
		margin-bottom: 29px;
	}
}

.social-icon-box {
	box-shadow: none;
	background: transparent;

	&.app-icon-box {
		box-shadow: none;
		background: transparent;
		padding: 58px 25px 44px;
		border-top: 6px solid transparent;
		border-radius: 0;
		transition: all 0.3s ease-in-out;

		&:hover {
			border-color: $color__theme-9;
			box-shadow: 0px 30px 60px 0px rgba(130, 5, 82, 0.1),
				0px -6px 0px 0px rgba(212, 51, 150, 0.004);

			a {
				opacity: 1;
			}
		}

		i {
			font-size: 20px;
			vertical-align: middle;
			margin-left: 8px;
		}

		a {
			color: #0d2244;
			opacity: 0;

			&:hover {
				color: $color__theme-9;
			}
		}
	}

	&.app-icon-box-two {
		padding: 38px;
		border-radius: 10px;
		background-color: rgb(255, 255, 255);
		box-shadow: 0px 2px 4px 0px rgba(0, 6, 83, 0.1);
		transition: all 0.3s ease-in-out;
		border-top: 3px solid transparent;

		@media (max-width: 991px) {
			margin-bottom: 30px;
		}

		.icon {
			border-radius: 50%;
			background-color: rgb(67, 51, 101, 0.039);
			display: flex;
			align-items: center;
			justify-content: center;
			height: 80px;
			width: 80px;
			margin-bottom: 30px;
		}

		.view-link {
			display: inline-flex;
			color: #0d2244;
			align-items: center;
			gap: 5px;

			&:hover {
				color: $color__theme-10;
			}
		}

		&:hover {
			box-shadow: 0px -3px 0px 0px rgba(117, 62, 243, 0.004),
				0px 20px 40px 0px rgba(25, 0, 84, 0.1);
			transition: translateY(-5px);
			border-color: $color__theme-10;
		}
	}

	h3 {
		font-size: 22px;
		font-weight: 600;
		margin-bottom: 15px;
		color: #0d2244;
		line-height: 1;
	}

	p {
		margin-bottom: 25px;
		color: #606e83;
		line-height: 28px;

		@media (max-width: 1200px) {
			br {
				display: none;
			}
		}
	}
}

