.banner {
	position: relative;

	.banner-content-wrap {
		position: relative;
		transform: translateY(52%);
		z-index: 2;
	}

	.banner-content {
		.banner-title {
			font-size: 60px;
			line-height: 70px;
			font-weight: 400;
			color: #fff;
			margin-bottom: 20px;

			span {
				font-weight: 800;
			}
		}

		.description {
			font-size: 18px;
			font-weight: 400;
			color: #fff;
			line-height: 30px;
			margin-bottom: 47px;
		}

		.banner-btn {
			color: #fff;
			padding: 10px 40px;
			border: 1px solid #fff;
			border-radius: 30px;
			font-size: 14px;
			display: inline-block;

			&:hover {
				background: #fff;
				color: $color__theme;
				box-shadow: 0px 20px 30px 0px rgba(79, 35, 35, 0.15);
			}
		}
	}

	&.banner-one {
		height: 1050px;
		background-image: $gradient_background;
		position: relative;
		overflow-x: hidden;

		.circle-shape {
			position: absolute;
			top: -20%;
			left: -13%;
		}

		.promo-mockup {
			margin-left: -120px;

			img {
				max-width: 920px;
				animation: zoom 4s infinite ease;
				animation-direction: alternate;
				transform: scale(1) translate(0px);
			}
		}

		.bg-shape {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 1;
			width: 100%;

			img {
				width: 100%;
			}
		}
	}

	&.banner-two {
		height: 1028px;
		overflow: hidden;
		background-image: $gradient_background_2;
		overflow-x: hidden;

		.vector-bg {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			// height: 1028px;

			img {
				max-width: 100%;
				height: 100%;
			}
		}

		.banner-content-wrap {
			transform: translateY(37%);
		}

		.banner-content {
			.banner-title {
				color: $color__primary;
			}

			.description {
				color: $color__primary;
			}

			.banner-btn {
				&.color-two {
					background: $color__theme-2;
					box-shadow: 0px 20px 30px 0px rgba(90, 70, 176, 0.3);
					border: 1px solid transparent;
				}

				&:hover {
					box-shadow: none;
					color: #fff;
				}
			}
		}

		.promo-mockup {
			img {
				max-width: 850px;
			}
		}
	}

	&.banner-three {
		background-image: $gradient_background;
		height: 950px;
		min-height: 700px;

		.banner-content-wrap-two {
			padding-top: 185px;
		}

		.banner-content {
			margin-bottom: 100px;
			position: relative;
			z-index: 22;

			.banner-title {
				font-size: 50px;
				line-height: 60px;
				font-weight: 800;
				margin-bottom: 20px;
			}

			.description {
				font-size: 18px;
				font-weight: 400;
				color: #fff;
				line-height: 30px;
				margin-bottom: 47px;
			}

			.banner-btn {
				color: #fff;
				padding: 8px 47px;
				border: 1px solid #fff;
				border-radius: 30px;
				font-size: 14px;
				display: inline-block;
				font-weight: 600;

				&.btn-fill {
					color: $color__theme;
				}

				&:hover {
					background: #fff;
					color: $color__theme;
					box-shadow: 0px 20px 30px 0px rgba(79, 35, 35, 0.15);
				}
			}
		}

		.play-btn {
			margin-left: 20px;
			font-weight: 500;
		}

		.circle-shape,
		.shape {
			position: absolute;
			z-index: 0;
			top: 0;
		}

		.circle-shape {
			left: 0;
		}

		.shape {
			right: 0;
		}

		.promo-mockup {
			position: relative;
			max-width: 970px;
			margin: 0 auto;

			img {
				box-shadow: 0px 60px 100px 0px rgba(79, 35, 35, 0.2);
				border-radius: 10px;
			}

			.shape-shadow {
				border-radius: 10px;
				background-color: rgb(255, 255, 255);
				opacity: 0.4;
				box-shadow: 0px 60px 100px 0px rgba(79, 35, 35, 0.2);
				height: 100px;
				max-width: 73%;
				position: absolute;
				bottom: -40px;
				width: 100%;
				z-index: -1;
				left: 50%;
				transform: translateX(-50%);
			}
		}

		.bg-shape-inner {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			overflow: hidden;

			img {
				width: 100%;
			}
		}
	}

	&.banner-four {
		background-image: $gradient_background_2;
		height: 1060px;
		padding-top: 290px;

		.banner-content-wrap-two {
			padding-top: 185px;
		}

		.banner-content {
			margin-bottom: 100px;
			position: relative;
			z-index: 22;

			.banner-title {
				font-size: 60px;
				line-height: 76px;
				font-weight: 700;
				margin-bottom: 20px;

				span {
					font-weight: 300;
				}
			}

			.description {
				font-size: 18px;
				font-weight: 400;
				color: #fff;
				line-height: 30px;
				margin-bottom: 47px;
			}
		}

		.promo-mockup {
			position: relative;
			z-index: 2;
			margin-top: -80px;
			animation: jump 2s infinite;
		}

		.play-btn {
			font-weight: 400;

			i {
				color: $color__theme-2;
				text-shadow: none;
			}
		}

		.bg-shape-inner {
			position: absolute;
			bottom: -2px;
			width: 100%;
		}

		.bottom-shape {
			img {
				width: 100%;
			}
		}
	}

	.banner-button-container {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&.banner-five {
		height: 930px;
		padding: 230px 0;
		background-image: linear-gradient(
			-60deg,
			rgb(112, 82, 251) 0%,
			rgb(138, 96, 253) 100%
		);

		.banner-right-shape {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}

		.banner-content-wrap-five {
			position: relative;
			z-index: 33;

			.banner-content {
				margin-top: 98px;

				.sub-title {
					font-size: 16px;
					text-transform: uppercase;
					color: $color__theme-2;
					font-weight: 400;
					letter-spacing: 2px;
					margin-bottom: 15px;
				}

				.banner-title {
					color: #322d49;
					font-size: 60px;
					line-height: 64px;
					font-weight: 500;
				}

				.description {
					color: #5e5b74;
					font-size: 18px;
					line-height: 30px;
					margin-bottom: 42px;
				}

				.banner-btn {
					background: $color__theme-2;
					border-radius: 4px;
					border: 1px solid transparent;
					box-shadow: 0px 20px 30px 0px rgba(90, 70, 176, 0.3);

					&:hover {
						color: $color__theme-2;
						background: transparent;
						border-color: $color__theme-2;
						box-shadow: none;
					}
				}
			}
		}
	}

	&.banner-six {
		height: 950px;
		position: relative;
		padding-top: 225px;
		overflow: hidden;

		.banne-content-wrapper-six {
			position: relative;
			z-index: 2;

			.subtitle {
				font-size: 16px;
				font-weight: 400;
				color: $color__theme-4;
				text-transform: uppercase;
				letter-spacing: 3px;
				margin-bottom: 13px;
			}

			.banner-title {
				font-size: 50px;
				line-height: 60px;
				font-weight: 600;
				margin-bottom: 20px;
			}

			p {
				margin-bottom: 40px;
			}
		}

		&:after {
			content: "";
			position: absolute;
			z-index: -1;
			width: 43%;
			height: 100%;
			right: 0;
			top: 0;
			background: #fdf5fe;
		}

		.banner-six-promo-image {
			position: relative;
			z-index: 2;

			> img {
				border-radius: 10px;
				box-shadow: 0px 30px 40px 0px rgba(1, 50, 5, 0.14);
			}

			.dot-shape {
				display: block;
				position: absolute;
				bottom: -40px;
				left: -40px;
				z-index: -1;
				animation: wave 8s infinite linear;
			}
		}

		.bottom-shape-coin {
			margin: 0;
			padding: 0;
			list-style: none;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;

			li {
				position: absolute;
				bottom: 0;

				&:nth-child(1) {
					left: -50px;
				}

				&:nth-child(2) {
					left: 60px;
				}

				&:nth-child(3) {
					left: 150px;
					bottom: 35px;
				}

				&:nth-child(4) {
					bottom: 20px;
					left: 37%;
				}

				&:nth-child(5) {
					top: 35%;
					left: 10%;
				}
			}
		}

		.particles-six {
			span {
				display: block;
				position: absolute;

				&.angle {
					top: 140px;
					left: 90px;
					animation: animationFramesOne 20s infinite linear;
				}

				&.dotsm {
					height: 15px;
					width: 15px;
					background: #43d355;
					left: 50%;
					top: 15%;
					border-radius: 50%;
					animation: animationFramesTwo 25s infinite linear;
				}

				&.dotmd {
					height: 15px;
					width: 15px;
					background: #f8cc61;
					border-radius: 50%;
					top: 50%;
					left: 15%;
					animation: animationFramesRight 20s infinite linear;
				}
			}
		}
	}

	&.banner-seven {
		height: 950px;
		background: #fff;
		padding-top: 230px;

		@media (max-width: 991px) {
			padding-top: 150px;
			height: 800px;
		}

		@media (max-width: 576px) {
			padding-bottom: 60px;
			height: auto;
		}

		.banner-content {
			.banner-title {
				font-size: 44px;
				line-height: 56px;
				font-weight: 500;

				@media (max-width: 991px) {
					font-size: 36px;
					line-height: 46px;

					br {
						display: none;
					}
				}

				@media (max-width: 576px) {
					font-size: 32px;
					line-height: 42px;
				}
			}

			.description {
				@media (max-width: 991px) {
					br {
						display: none;
					}
				}
			}
		}

		.job-search-form-wrapper {
			max-width: 770px;
			margin: 0 auto 115px;

			@media (max-width: 576px) {
				margin: 0 auto 80px;
			}

			.job-form-inner {
				display: flex;
				border-radius: 4px;
				overflow: hidden;

				input {
					border-radius: 0;
					margin: 0;
				}

				button {
					min-width: 150px;
					border: 0;
					background: $color__theme-5;
					color: #fff;
				}
			}
		}

		.promo-mockup {
			border-radius: 20px;
			overflow: hidden;
			box-shadow: 0px 50px 70px 0px rgba(10, 1, 47, 0.12);
			max-width: 1030px;
			margin: 0 auto;
		}
	}

	&.banner-eight {
		position: relative;
		height: 1050px;
		min-height: 100vh;

		@media (max-width: 991px) {
			height: auto;
		}

		.banne-content-wrapper-eight {
			position: relative;
			z-index: 222;

			@media (max-width: 991px) {
				padding-top: 120px;
			}

			.subtitle {
				font-size: 12px;
				color: #1a133b;
				background: #f0f2f7;
				padding: 5px 40px 5px 5px;
				border-radius: 10px;
				display: inline-block;

				span {
					background: $color__theme-6;
					color: #fff;
					padding: 0 9px;
					border-radius: 20px;
					display: inline-block;
					line-height: 1.4;
					margin-right: 20px;
				}
			}

			.banner-title {
				font-size: 70px;
				font-weight: 600;
				color: #1a133b;
				line-height: 1.2;
				margin-bottom: 12px;

				@media (max-width: 1200px) {
					font-size: 55px;
				}

				@media (max-width: 768px) {
					font-size: 40px;
				}

				@media (max-width: 768px) {
					font-size: 36px;
				}

				span {
					font-weight: 300;
				}
			}

			p {
				margin-bottom: 40px;
			}

			.banner-button-container {
				justify-content: unset;

				.banner-btn {
					padding: 10px 38px;
					font-size: 16px;
					margin-right: 20px;
				}
			}
		}

		.banner-six-promo-image {
			padding-top: 195px;
			position: relative;

			@media (max-width: 991px) {
				&.text-right {
					text-align: center !important;
				}
			}

			@media (max-width: 1400px) {
				padding-top: 130px;
			}

			@media (max-width: 991px) {
				padding-top: 50px;
				max-width: 480px;
				margin: 0 auto;
			}

			img {
				position: relative;
				z-index: 22;
				margin-right: -55px;

				@media (max-width: 991px) {
					margin-right: 0;
				}
			}

			.banner-leaf {
				position: absolute;
				bottom: 120px;
				left: 0;
				width: 100%;
				z-index: 1;

				.leaf-left,
				.leaf-right {
					position: absolute;
					bottom: 0;
				}

				.leaf-left {
					left: 65px;

					@media (max-width: 1200px) {
						left: -30px;
					}

					@media (max-width: 991px) {
						left: -65px;
					}
				}

				.leaf-right {
					right: -15px;
					bottom: 65px;
				}
			}
		}

		.banner-background-element {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;

			.dot-shape,
			.circle-bg,
			.ball,
			.triangle {
				position: absolute;
			}

			.ball {
				left: 100px;
				top: 190px;

				@media (max-width: 1400px) {
					left: 20px;
				}
			}

			.triangle {
				bottom: 150px;
				left: 40%;
			}

			.dot-shape {
				right: 40px;
				top: 40px;
			}

			.circle-bg {
				height: 1100px;
				width: 1100px;
				background-image: linear-gradient(
					-120deg,
					rgb(253, 151, 32) 0%,
					rgb(18, 191, 124) 100%
				);
				opacity: 0.102;
				border-radius: 50%;
				top: -14%;
				right: -3.2%;
				z-index: -1;

				@media (max-width: 1400px) {
					height: 900px;
					width: 900px;
					top: -4%;
					right: -7%;
				}

				@media (max-width: 991px) {
					height: 600px;
					width: 600px;
					top: -2%;
					right: -3%;
				}
			}
		}
	}

	&.banner-nine {
		background: #f8f9fd;
		height: 1020px;
		padding-top: 227px;
		background-size: cover;
		background-position: center center;

		@media (max-width: 991px) {
			padding: 140px 0 100px;
			height: auto;
		}

		.banner-content {
			max-width: 640px;
			margin: 0 auto 65px;

			.banner-title {
				color: #1a133b;
				font-size: 50px;
				font-weight: 600;
				margin-bottom: 13px;

				@media (max-width: 768px) {
					font-size: 34px;
					line-height: 45px;
				}

				span {
					font-weight: 400;
				}
			}

			.description {
				color: #777580;
				margin-bottom: 40px;
			}

			.banner-btn {
				border-radius: 4px;
				border: 2px solid $color__theme-7;
				color: $color__theme-7;
				background: transparent;
				box-shadow: none;
				padding: 9px 38px;
				font-size: 16px;

				&:hover {
					color: #fff;
					background: $color__theme-7;
				}
			}
		}
	}

	&.banner-ten {
		background: #040532;
		height: 100vh;
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;

		.banner-content-wrapper-ten {
			.banner-title {
				color: #fff;
				margin-bottom: 27px;
				font-size: 44px;

				@media (max-width: 1200px) {
					br {
						display: none;
					}
				}

				@media (max-width: 991px) {
					font-size: 40px;
				}

				@media (max-width: 767px) {
					font-size: 30px;
				}
			}

			.description {
				color: #fff;
				margin-bottom: 50px;
				line-height: 28px;
			}

			@media (max-width: 991px) {
				text-align: center;
				margin-bottom: 40px;
			}
		}

		.banner-btn {
			padding: 7px 34px;
			font-size: 16px;
			font-weight: 500;

			&.btn-outline {
				margin-left: 20px;
				color: #fff;
				border-color: rgba(255, 255, 255, 0.302);

				&:hover {
					color: $color__theme-8;
				}

				@media (max-width: 380px) {
					margin-left: 0;
					margin-top: 20px;
				}
			}
		}

		.banner-six-promo-image {
			text-align: center !important;
		}

		@media (max-width: 991px) {
			height: auto;
			padding: 150px 0 100px;
		}
	}

	&.banner-eleven {
		height: 100vh;
		background-image: url(../../media/banner/banner_travel.png);
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		display: flex;
		align-items: center;

		.subtitle {
			font-family: "Satisfy", cursive;
			font-size: 20px;
			color: #fff;
			font-weight: 400;
			margin-bottom: 5px;
		}

		.banner-title {
			font-size: 70px;
			color: #fff;
			font-weight: 700;

			@media (max-width: 992px) {
				font-size: 50px;
			}

			@media (max-width: 576px) {
				font-size: 40px;
			}
		}

		.description {
			color: #fff;
			font-size: 20px;
			margin-bottom: 45px;
		}

		.banner-button-container {
			justify-content: left;
		}

		.banner-btn {
			border-color: rgba(255, 255, 255, 0.302);
			padding: 15px 32px;
			border-radius: 10px;
			line-height: 1;
			display: inline-flex;
			align-items: center;
			margin-right: 20px;

			i {
				margin-left: 6px;
				font-size: 20px;
			}

			&:hover {
				color: $color__theme-2;
				box-shadow: none;
			}
		}

		.play-btn {
			color: #fff;

			i {
				border-color: #fff;
				text-shadow: 0px 6px 6px rgba(118, 43, 255, 0.4);
				color: $color__theme-2;
			}
		}
	}

	&.banner-ai-image {
		background-color: #050c1e;
		padding: 232px 0 110px;
		background-image: url(../../media/banner/ai/banner-ai.png);
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;

		@media (max-width: 1024px) {
			padding: 180px 0 100px;
		}

		@media (max-width: 1024px) {
			padding: 120px 0 100px;
		}

		@media (max-width: 991px) {
			padding: 100px 0 80px;
		}

		.banner-content {
			margin-bottom: 120px;

			@media (max-width: 991px) {
				margin-bottom: 80px;
			}

			@media (max-width: 767px) {
				margin-bottom: 60px;
			}
		}

		.banner-title {
			font-family: "Montserrat Alternates";
			font-size: clamp(40px, 5vw, 70px);
			line-height: 1.2;
			font-weight: 500;
			margin-bottom: 44px;

			@media (max-width: 991px) {
				br {
					display: none;
				}
			}
		}
	}

	.banner-ai__images {
		display: flex;
		align-items: center;
