.animate-particle {
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	padding: 0;
	list-style: none;
	height: 100%;
	width: 100%;
	z-index: 22;

	li {
		position: absolute;

		&:nth-child(1) {
			left: 90px;
			top: 140px;
			animation: animationFramesRight 40s infinite linear;
		}

		&:nth-child(2) {
			left: 40%;
			bottom: 13%;
			animation: animationFramesRight 30s infinite linear;
		}

		&:nth-child(3) {
			right: 28%;
			top: 14%;
			animation: animationFramesRight 30s infinite linear;
		}

		&:nth-child(4) {
			right: 5%;
			top: 63%;
			animation: animationFramesLeft 20s infinite linear;
		}

		&:nth-child(5) {
			left: 50%;
			top: 25%;
			animation: animationFramesLeft 20s infinite linear;
		}

		&:nth-child(6) {
			right: -9%;
			top: 110px;
		}

		&.bubble {
			height: 10px;
			width: 10px;
			background: #a2e3f6;
			border-radius: 50%;
			left: 15%;
			top: 28%;
			animation: animationFramesLeft 30s infinite linear;
		}
	}
}

