// Banner App Landing
.banner-app-two {
	background-image: linear-gradient(-335deg, #6541c1 30%, #d43396 70%);
	height: 950px;
	display: flex;
	align-items: center;
	justify-content: center;

	@media (max-width: 991px) {
		height: auto;
		padding: 100px 0;
	}

	.banner-content {
		margin-top: 96px;

		.banner-title {
			font-size: 70px;
			font-weight: 700;
			color: #f9fbff;
			line-height: 74px;
			margin-bottom: 20px;

			@media (max-width: 1200px) {
				font-size: 50px;
				line-height: 60px;
			}

			@media (max-width: 991px) {
				font-size: 40px;
				line-height: 44px;
			}
		}

		p {
			font-size: 20px;
			line-height: 32px;
			font-weight: 400;
			color: #f9fbff;
			margin-bottom: 48px;

			@media (max-width: 1200px) {
				font-size: 16px;

				br {
					display: none;
				}
			}

			@media (max-width: 1024px) {
				br {
					display: none;
				}
			}
		}

		.banner-btn {
			background-color: #fff;
			color: $color__theme-9;
			box-shadow: none;
			padding: 14px 35px;
			border: 2px solid #fff;

			&:hover {
				background-color: transparent;
				color: #fff;
			}
		}

		@media (max-width: 991px) {
			margin-top: 0;
			text-align: center;
		}
	}

	.banner__app-image {
		position: relative;
		z-index: 5;
		top: 34px;

		@media (max-width: 991px) {
			text-align: center;
		}

		.banner__app-image-one {
			position: absolute;
			top: 102px;
			left: -8px;

			@media (max-width: 1200px) {
				left: -80px;
				max-width: 225px;
			}

			@media (max-width: 1024px) {
				max-width: 200px;
				left: -20px;
			}

			@media (max-width: 991px) {
				left: 22%;
			}

			@media (max-width: 768px) {
				left: 15%;
			}

			@media (max-width: 575px) {
				max-width: 150px;
			}

			@media (max-width: 410px) {
				left: 4%;
				max-width: 130px;
			}
		}

		.banner__app-image-two {
			position: absolute;
			right: -78px;

			@media (max-width: 1200px) {
				right: -10px;
				max-width: 225px;
			}

			@media (max-width: 1024px) {
				max-width: 200px;
			}

			@media (max-width: 991px) {
				position: relative;
				right: -150px;
				margin: 0 auto;
			}

			@media (max-width: 575px) {
				max-width: 130px;
				right: -75px;
			}
		}
	}

	.app-banner-left {
		position: absolute;
		top: 50px;
		left: 0;
	}

	.app-banner-center {
		position: absolute;
		left: 33%;
		top: 0;
	}
}
