#testimonial-wrapper-three {
	max-width: 1060px;
	margin: 0 auto;
	position: relative;

	.quote {
		position: absolute;
		bottom: 80px;
		right: 80px;
		z-index: 22;
	}

	.swiper-container {
		padding: 0 45px 80px;
	}

	.slider-nav {
		width: 90px;
		top: auto;
		bottom: 100px;
		left: 50%;
		height: 40px;

		.swiper-button-prev {
			height: 25px;
			transition: all 0.3s ease-in-out;
			outline: 0;

			.arrow {
				margin-top: 12px;
				width: 25px;
				height: 2px;
				background: #bdb4b4;
				transition: all 0.3s ease-in-out;

				&:after {
					content: "";
					position: absolute;
					width: 15px;
					height: 2px;
					top: 7px;
					left: -2px;
					background-color: #bdb4b4;
					transform: rotate(-45deg);
					transition: all 0.3s ease-in-out;
				}

				&:before {
					content: "";
					position: absolute;
					width: 15px;
					height: 2px;
					top: 17px;
					left: -2px;
					background-color: #bdb4b4;
					transform: rotate(45deg);
					transition: all 0.3s ease-in-out;
				}
			}

			&:hover {
				width: 40px;
				left: -15px;

				.arrow {
					width: 40px;
					background: $color__theme;

					&:before,
					&:after {
						background: $color__theme;
					}
				}
			}
		}

		.swiper-button-next {
			height: 25px;
			transition: all 0.3s ease-in-out;

			.arrow {
				margin-top: 12px;
				width: 25px;
				height: 2px;
				background: #bdb4b4;
				transition: all 0.3s ease-in-out;

				&:after {
					content: "";
					position: absolute;
					width: 15px;
					height: 2px;
					top: 7px;
					right: -2px;
					background-color: #bdb4b4;
					transform: rotate(45deg);
					transition: all 0.3s ease-in-out;
				}

				&:before {
					content: "";
					position: absolute;
					width: 15px;
					height: 2px;
					top: 17px;
					right: -2px;
					background-color: #bdb4b4;
					transform: rotate(-45deg);
					transition: all 0.3s ease-in-out;
				}
			}

			&:hover {
				width: 40px;
				right: -15px;

				.arrow {
					width: 40px;
					background: $color__theme;

					&:before,
					&:after {
						background: $color__theme;
					}
				}
			}
		}
	}
}

