.testimonial-hots {
	text-align: center;
	margin: 0 auto;
	padding: 60px 60px 0 60px;
	position: relative;
	border-top: 2px solid #f0f0f0;
	border-left: 2px solid #f0f0f0;
	border-right: 2px solid #f0f0f0;

	@media (max-width: 768px) {
		padding: 40px 30px 0 40px;
	}

	@media (max-width: 768px) {
		padding: 30px 30px 0 30px;
	}

	&:before,
	&:after {
		content: "";
		position: absolute;
		height: 2px;
		width: 28%;
		background: #f0f0f0;

		@media (max-width: 991px) {
			width: 18%;
		}

		@media (max-width: 768px) {
			width: 7%;
		}
	}

	&:before {
		left: 0;
		bottom: 0;
	}

	&:after {
		right: 0;
		bottom: 0;
	}

	* {
		transform: translateY(-20px);
		opacity: 0;
		transition: all 0.5s;
	}

	.swiper-slide-active & {
		* {
			transform: translateY(0);
			opacity: 1;
		}

		.testimonial-content {
			transition-delay: 0.3s;
		}

		.bottom-info {
			transition-delay: 0.5s;
		}
	}

	.testimonial-content {
		max-width: 670px;
		margin: 0 auto;

		p {
			font-size: 18px;
			line-height: 36px;

			@media (max-width: 480px) {
				font-size: 16px;
				line-height: 30px;
			}
		}
	}

	.bottom-info {
		display: flex;
		justify-content: space-around;
		transform: translateY(50px);
		margin-left: 55px;

		@media (max-width: 480px) {
			margin-left: 0;
		}
	}

	.author-wrapper {
		display: flex;
		align-items: center;
		text-align: left;

		.single-bio-thumb {
			border-radius: 4px;
			margin-right: 35px;
			position: relative;

			@media (max-width: 480px) {
				margin-right: 20px;
				width: 80px;
				height: 100px;
			}

			img {
				border-radius: 4px;
				position: relative;
				z-index: 2;
			}

			&:before {
				content: "";
				position: absolute;
				border-radius: 4px;
				width: 130px;
				height: 170px;
				border: 2px solid #f0f0f0;
				top: -20px;
				left: -60px;

				@media (max-width: 480px) {
					width: 80px;
					height: 118px;
					border: 2px solid #f0f0f0;
					top: -15px;
					left: -17px;
				}
			}
		}

		.bio-info {
			.name {
				font-size: 20px;
				color: #2c234d;
				margin: 0;

				@media (max-width: 480px) {
					font-size: 16px;
